import React from 'react';
import '../style/History.scss';
import map from '../images/caribbeanMap.png';
import pirateStuff from '../images/pirateStuff.png';
const History = () => {
    return (
        <div id="history" className="history">
            <div className="history__content">
                <div className="history__main">
                    <img className="historyMap" src={map} alt="mapa karaibw" />
                    <div className="historyInfo">
                        <h1 className="historyInfo__title">O Karaibach</h1>
                        <p className="historyInfo__informations">Karaiby zostay odkryte w 1492 roku, przez Krzysztofa Kolumba. Nastpnie cay obszar zosta szybko skolonizowany przez cywilzacje zachodnie, stajc si wan czci szlakw handlowych, przez co stay si miejscem czstego piractwa. Do koca XVII wieku z Tortugi wysyano pirackie ekspedycje w celu oblegania i rabowania nadmorskich miast. Wan siedzib piratw by take Port Royal. Od XIX wieku kolonie stay si niepodlege.</p>
                    </div>
                </div>
                <img className="history__footer" src={pirateStuff} alt="pirackie rzeczy" />
            </div>

        </div>
    );
}

export default History;